.contact-form-container{max-width:1000px;margin:0 auto;padding:2rem;background:#3f2a56;background:linear-gradient(0deg, rgb(63, 42, 86) 0%, rgb(99, 43, 99) 100%);border-radius:8px;box-shadow:0 2px 10px rgba(0,0,0,.989)}.contact-form-container h2{text-align:center;font-size:2rem;margin-bottom:1.5rem;color:#fcfafa}.contact-form{display:flex;flex-direction:column;gap:1.5rem}.form-group{display:flex;flex-direction:column;gap:.5rem}input[type=text],input[type=email],textarea{padding:.8rem;font-size:1rem;border:1px solid #ccc;border-radius:4px;outline:none;transition:border .3s ease}input[type=text]:focus,input[type=email]:focus,textarea:focus{border-color:#dfcc4e}.form-button{display:flex;justify-content:center}