.button{background-color:#c6953b;border-radius:6px;border-width:0;box-shadow:rgba(50,50,93,.1) 0 0 0 1px inset,rgba(50,50,93,.1) 0 2px 5px 0,rgba(0,0,0,.07) 0 1px 1px 0;box-sizing:border-box;color:#fff;cursor:pointer;font-size:100%;font-weight:600;height:44px;line-height:1.15;margin:12px 0 0;outline:none;overflow:hidden;padding:0 25px;position:relative;text-align:center;text-transform:none;transform:translateZ(0);transition:all .2s,box-shadow .08s ease-in;user-select:none;width:30%;text-shadow:2px 2px 2px rgba(0,0,0,.4)}.button:disabled{cursor:default}.button:hover{background-color:#59978a}.button:focus{box-shadow:rgba(50,50,93,.1) 0 0 0 1px inset,rgba(50,50,93,.2) 0 6px 15px 0,rgba(0,0,0,.1) 0 2px 2px 0,rgba(50,151,211,.3) 0 0 0 4px}